import { Injectable } from '@angular/core';
import { IStudent } from '../models/IStudent';
import { BehaviorSubject } from 'rxjs/BehaviorSubject';
import { Storage } from '@ionic/storage';
import { ToastController } from 'ionic-angular';

@Injectable()
export class StudentService {

    public students: BehaviorSubject<IStudent[]>;

    public _students = [
        {
            id: 1,
            name: 'Valeriy',
            lastname: 'Kondratko',
            age: 28,
            tel: '+79516376127',
            email: 'kve1990@yandex.ru',
            addres: '...',
            hourscount : 0,
            group: 57
        }
    ];

    constructor(private storage: Storage, public toastCtrl: ToastController) {
        this.students = new BehaviorSubject([]);
        this.getStudents();
    }

    getStudents() {
        this.storage.get('students')
            .then(res => {
            if (res && res.length) {
                this.students.next(res);
            } else {
                this.storage.set('students', this._students)
                    .then(res => this.students.next(res));
            }
        });
    }

    setStudent(student) {
        let oldStudents = this.students.getValue();
        let index = oldStudents.findIndex(item => item.id === student.id);
        let msg = '';
        if ( ~index ) {
            oldStudents[index] = student;
            msg = 'Данные об обучаемом успешно изменены!';
            this.storage.set('students', [...oldStudents])
                .then(res => this.students.next(res));
        } else {
            msg = 'Обучаемый успешно добавлен';
            this.storage.set('students', [...oldStudents, student])
                .then(res => this.students.next(res));
        }
        const toast = this.toastCtrl.create({
            message: msg,
            duration: 3000
        });
        toast.present();
    }

    deleteStudent(student) {
        let students = this.students.getValue();
        let index = students.findIndex(item => item.id === student.id);
        students.splice(index, 1);
        this.students.next(students);
    }

}


