import { Component } from '@angular/core';
import { Platform, NavParams, ViewController } from 'ionic-angular';
import { IStudent } from '../../models/IStudent';
import { StudentService } from '../../services/students.service';
import { GroupService } from '../../services/groups.service';

@Component({
    selector: 'page-modal',
    templateUrl: 'modal-page.html'
})
export class ModalPage {

    public student: IStudent;
    public students;
    public groups: number[];
    public isEditing = false;
    public isNewStudent: boolean;

    constructor(
        public platform: Platform,
        public params: NavParams,
        public studentsService: StudentService,
        public groupsService: GroupService,
        public viewCtrl: ViewController
    ) {
        let studentId = this.params.get('studentId');
        let selectedGroup = this.params.get('selectedGroup');
        console.log('group', selectedGroup);
        this.studentsService.students.subscribe(res => {
            this.students = res;
            if (studentId) {
                this.student = this.students.find(student => student.id === studentId);
            } else {
                this.isNewStudent = true;
                this.student = {
                    id: Date.now(),
                    name: '',
                    lastname: '',
                    age: 0,
                    tel: '',
                    address: '',
                    email: '',
                    group: 59,
                    hourscount: 0
                };
            }
        });

        this.groupsService.groups.subscribe(groups => this.groups = groups);
    }

    ngOnInit() {
        this.isEditing = this.isNewStudent;
    }

    dismiss() {
        this.viewCtrl.dismiss();
    }

    save() {
        this.studentsService.setStudent(this.student);
        this.dismiss();
    }

    delete() {
        this.studentsService.deleteStudent(this.student);
        this.dismiss();
    }

}


