import { Injectable } from '@angular/core';
import { BehaviorSubject } from 'rxjs/BehaviorSubject';
import { AlertController } from 'ionic-angular';
import { Storage } from '@ionic/storage';

@Injectable()
export class GroupService {

    public groups: BehaviorSubject<any>;

    constructor(private storage: Storage, public alertCtrl: AlertController) {
        this.groups = new BehaviorSubject([]);

        storage.get('groups')
            .then(res => {
                if (res && res.length) {
                    this.groups.next(res);
                } else {
                    storage.set('groups', [{id: 57, description: 'First group'}])
                        .then(res => this.groups.next(res));
                }
            });
    }

    addGroup(id) {
        let groups = this.groups.getValue();
        if (~groups.findIndex(group => group.id === id)) {
            const alert = this.alertCtrl.create({
                title: 'Группа уже существует!',
                subTitle: 'Группа с таким номером уже существует!',
                buttons: ['OK']
            });
            alert.present();
        } else {
            this.storage.set('groups', [...groups, {id, description: ''}])
                .then(res => this.groups.next(res));
        }
    }

    deleteGroup(id) {
        let groups = this.groups.getValue();
        let index = groups.findIndex(group => group.id === id);
        if (~index) {
            groups.splice(id, 1);
            this.groups.next(groups);
        }
    }

}


