import { Pipe, PipeTransform } from '@angular/core';

@Pipe({
    name: 'groupFilter',
    pure: false
})
export class GroupFilterPipe implements PipeTransform {
    transform(items, arg): any {
        return items.filter(item => item.group === arg );
    }
}



