import { Component } from '@angular/core';
import { NavController } from 'ionic-angular';
import { StudentService } from '../../services/students.service';
import { EventsService } from '../../services/events.service';
import { ModalController } from 'ionic-angular';
import { ModalEvent } from './modal-event';

declare var Timetable;

@Component({
  selector: 'page-about',
  templateUrl: 'about.html'
})
export class AboutPage {



  public groups;
  public selectedGroup;
  public students;
  public selectedStudent;
  public date = new Date(Date.now()).toISOString().slice(0, 10);
  public timeoffset = 'day';
  public timetable;
  public daysOfWeek = ['Пн', 'Вт', 'Ср', 'Чт', 'Пт', 'Сб', "Вс"];
  public events;

  constructor(public navCtrl: NavController,
              public studentsService: StudentService,
              public modalCtrl: ModalController,
              public eventsService: EventsService) {
    
  }

  onWatch(evt){
    if (evt === 'week') {
      setTimeout(()=>{
        this.renderTable();
      });
    }
  }

  ngOnInit() {
    this.studentsService.students.subscribe(students => this.students = students);
    this.eventsService.events.subscribe(events => {
      this.events = events;
      var container = document.querySelector('.timetable');
      if (this.events && this.events.length && container) {
        this.renderTable();
      }
    });
  }

  addEvent() {
    const modal = this.modalCtrl.create(ModalEvent);
    modal.present();
  }

  renderTable() {
    this.timetable = new Timetable();
    console.log(this.events);
    let minHour = this.events
      .map(event => (new Date(event.startTime)).getHours())
      .sort( (a, b) =>  a-b)[0];

    let maxHour = this.events
      .map(event => (new Date(event.endTime)).getHours())
      .sort( (a, b) =>  a-b)[this.events.length-1] + 1;
    this.timetable.setScope(minHour, maxHour);
    this.timetable.addLocations(this.daysOfWeek);
    this.events.forEach(event => {
      let student = this.students.find(student => student.id === event.studentId);
      let dayOfWeek = this.daysOfWeek[this.getDay(event.startTime)];
      this.timetable.addEvent(
        student.name,
        dayOfWeek,
        new Date(event.startTime),
        new Date(event.endTime)
      );
    });
    var renderer = new Timetable.Renderer(this.timetable);
    renderer.draw('.timetable');
  }

  getDay(date) {
    var d = (new Date(date)).getDay();
    return d === 0 ? 6 : d - 1;
  }
}



