import { Component } from '@angular/core';
import { Platform, NavParams, ViewController } from 'ionic-angular';
import { StudentService } from '../../services/students.service';
import { GroupService } from '../../services/groups.service';

@Component({
    selector: 'page-group',
    templateUrl: 'modal-group.html'
})
export class ModalGroup {

    public group;
    public groups;

    constructor(
        public platform: Platform,
        public params: NavParams,
        public studentsService: StudentService,
        public groupsService: GroupService,
        public viewCtrl: ViewController
    ) {
        this.groupsService.groups.subscribe(groups => {
            this.groups = groups;
            this.groups.sort( (a, b) =>  a.id - b.id);
            this.group = {
                id: groups[groups.length-1].id + 1,
                description: ''
            }
        });
    }

    dismiss() {
        this.viewCtrl.dismiss();
    }

    save() {
        this.groupsService.addGroup(this.group.id);
        this.dismiss();
    }

}


