import { Component } from '@angular/core';
import { NavController } from 'ionic-angular';
import { ModalController } from 'ionic-angular';
import { ModalPage } from './modal-page';
import { ModalGroup } from './modal-group';
import { StudentService } from '../../services/students.service';
import { GroupService } from '../../services/groups.service';

@Component({
  selector: 'page-contact',
  templateUrl: 'contact.html'
})
export class ContactPage {

  public groups;
  public students;
  public studentsOfGroup;
  public selectedGroup;

  constructor(public navCtrl: NavController,
    public studentsService: StudentService,
    public groupService: GroupService,
    public modalCtrl: ModalController) {
      this.groupService.groups.subscribe(groups => {
        this.groups = groups;
        if (this.groups && this.groups.length) {
          this.selectedGroup = this.groups[this.groups.length -1].id;
        }
      });
      this.studentsService.students.subscribe(students => this.students = students);
  }

  itemSelected(student) {
    this.presentModal(student);
  }

  addStudent() {
    this.presentModal({id: null});
  }

  addGroup() {
    const modal = this.modalCtrl.create(ModalGroup);
    modal.present();
  }

  presentModal(student) {
    const studentId = student.id;
    const selectedGroup = this.selectedGroup;
    const modal = this.modalCtrl.create(ModalPage, {studentId, selectedGroup});
    modal.present();
  }

  onSelectGroup(group) {
    this.studentsOfGroup = this.students.filter(student => student.group === group);
  }

}



