import { Injectable } from '@angular/core';
import { BehaviorSubject } from 'rxjs/BehaviorSubject';
import { Storage } from '@ionic/storage';

@Injectable()
export class EventsService {

    public events: BehaviorSubject<any>;
    private _events = [
        {
            id: 1,
            studentId: 1,
            startTime: (new Date(2016,7,18,10,41)).toISOString(),
            endTime: (new Date(2016,7,18,13,30)).toISOString()
        }
    ];

    constructor(private storage: Storage) {
        this.events = new BehaviorSubject([]);

        storage.get('events')
            .then(res => {
                if (res && res.length) {
                    this.events.next(res);
                } else {
                    storage.set('events', this._events)
                        .then(res => this.events.next(res));
                }
            });
    }

    addEvent(studentId, startTime, endTime) {
        let events = this.events.getValue();
        this.storage.set('events', [...events, {id: Date.now(), studentId, startTime, endTime}])
            .then(res => this.events.next(res));
    }

    deleteEvent(id) {
        let events = this.events.getValue();
        let index = events.findIndex(event => event.id === id);
        if (~index) {
            events.splice(id, 1);
            this.events.next(events);
        }
    }

}


