import { Component } from '@angular/core';
import { Platform, NavParams, ViewController } from 'ionic-angular';
import { StudentService } from '../../services/students.service';
import { GroupService } from '../../services/groups.service';
import { EventsService } from '../../services/events.service';

@Component({
    selector: 'page-event',
    templateUrl: 'modal-event.html'
})
export class ModalEvent {

    public groups;
    public students;
    public selectedGroup;
    public selectedStudent;
    public date;
    public startTime;
    public endTime;

    constructor(
        public platform: Platform,
        public params: NavParams,
        public studentsService: StudentService,
        public groupsService: GroupService,
        public eventsService: EventsService,
        public viewCtrl: ViewController
    ) {
        this.groupsService.groups.subscribe(groups => this.groups = groups);
        this.studentsService.students.subscribe(students => this.students = students);
    }

    dismiss() {
        this.viewCtrl.dismiss();
    }

    addEvent() {
        const startTime = `${this.date}T${this.startTime}`;
        const endTime = `${this.date}T${this.endTime}`;
        this.eventsService.addEvent(this.selectedStudent, startTime, endTime);
        this.dismiss();
    }

}


